"use strict";
import styled from "styled-components";
import { Flex, Separator, Text, useSporeColors } from "ui/src";
export default function ToggleOptions({
  activeOption,
  setActiveOption
}) {
  const colors = useSporeColors();
  const toggleButtons = ["Increase", "Extend", "Merge", "Transfer"];
  const Button = styled.button`
    all: unset;
    padding: 16px 34px;
    color: ${colors.neutral2.val};
    cursor: pointer;
    border-bottom: 1px solid transparent;
    transition: all 0.2s ease;

    &.active {
      border-color: ${colors.accent1.val};
      color: ${colors.neutral1.val};
    }

    @media (max-width: 768px) {
      padding: 8px 16px;
    }
  `;
  return <Flex flexDirection="column">
      <Flex row gap={"$gap12"} justifyContent="space-between">
        {toggleButtons.map((item) => <Button
    role="button"
    key={item}
    className={`${activeOption === item ? "active" : ""}`}
    onClick={() => setActiveOption(item)}
  >
            <Text variant="buttonLabel3" color={"inherit"}>
              {item}
            </Text>
          </Button>)}
      </Flex>
      <Separator width={"100%"} mt={"-1px"} height={1} />
    </Flex>;
}
